@font-face {
  font-family: 'feather';
  src: url('/static/fonts/feather/feather-webfont.eot'); /* IE9*/
  src: url('/static/fonts/feather/feather-webfont.eot')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('/static/fonts/feather/feather-webfont.woff')
      format('woff'),
    /* chrome, firefox */ url('/static/fonts/feather/feather-webfont.ttf')
      format('truetype'),
    /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
      url('/static/fonts/feather/feather-webfont.svg') format('svg'); /* iOS 4.1- */
}

.fe {
  font-family: 'feather' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

