@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline; }

html {
  line-height: 1; }

ol, ul {
  list-style: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

caption, th, td {
  text-align: left;
  font-weight: normal;
  vertical-align: middle; }

q, blockquote {
  quotes: none; }
  q:before, q:after, blockquote:before, blockquote:after {
    content: "";
    content: none; }

a img {
  border: none; }

article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary {
  display: block; }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

body {
  word-wrap: break-word;
  font-size: 14px;
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Noto Sans TC", "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  position: relative;
  background: #f8f8f8;
  overflow-x: hidden;
  touch-action: manipulation; }

a {
  text-decoration: none;
  color: #fff; }

.container {
  max-width: 1800px;
  padding-left: 4.16667%;
  padding-right: 4.16667%;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  box-sizing: border-box; }

main {
  position: relative;
  padding-top: 3em;
  margin-top: 62px;
  min-height: calc(100vh - 62px); }

header {
  position: fixed;
  width: 100%;
  height: 62px;
  top: 0;
  left: 0;
  font-weight: 500;
  font-size: 1.333em;
  color: #fff;
  letter-spacing: 1px;
  background-color: #24292e;
  z-index: 32;
  box-sizing: border-box; }
  header .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%; }
    header .header i {
      font-size: 1.333em; }

.filters {
  position: fixed;
  width: 270px;
  border-radius: 6px;
  border: 1px solid #24292e;
  overflow: hidden; }
  .filters__search {
    padding: 1em .75em;
    background-color: #24292e;
    box-sizing: border-box; }
  .filters__info {
    padding: 1.5em 1em 1em;
    box-sizing: border-box; }
    .filters__info h3 {
      margin-bottom: 1em;
      color: rgba(63, 68, 72, 0.5);
      letter-spacing: 2px;
      font-size: 1.083em; }
    .filters__info p {
      line-height: 2;
      font-size: 14px;
      color: rgba(63, 68, 72, 0.95); }
      .filters__info p span {
        margin-left: 1em; }

.lists {
  padding-left: calc(270px + 4em);
  box-sizing: border-box; }
  .lists__title {
    padding-bottom: .5em;
    font-size: 1.667em;
    line-height: 1.5;
    border-bottom: 1px solid #24292e;
    box-sizing: border-box; }
  .lists__container {
    margin-top: 2em; }
    .lists__container li {
      padding: 2em 0;
      border-bottom: 1px solid rgba(36, 41, 46, 0.2); }
      .lists__container li a {
        position: relative;
        display: block;
        padding-bottom: .5em;
        font-size: 1.667em;
        color: #0366d6;
        line-height: 1;
        box-sizing: border-box; }
      .lists__container li > p {
        line-height: 1;
        font-size: 1.417em;
        font-weight: 300;
        margin-bottom: .5em; }
  .lists__topic {
    display: flex;
    margin-bottom: .5em; }
    .lists__topic span {
      padding: .3em .9em;
      margin: 0 .5em .5em 0;
      background-color: #d8ebff;
      color: #0366d6;
      border-radius: 3px; }
  .lists__others {
    display: flex; }
    .lists__others p {
      display: flex;
      margin-right: 1em;
      justify-content: center;
      font-size: 1.167em;
      font-weight: 300; }
      .lists__others p i {
        margin-right: .5em; }

